@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,700;1,700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.section-p1 {
  padding: 40px 80px;
}
/*
font-family: 'Poppins', sans-serif;
font-family: "Playfair Display", serif;
*/

/*navbar*/
.navbar {
  font-family: "Poppins", serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: gray;
}

.navbar-brand {
  font-family: "Playfair Display", serif;
  text-transform: lowercase;
  font-weight: 700;
}

.nav-link {
  font-weight: 300 !important;
  margin-left: 21px;
  cursor: pointer;
}

.nav-link:hover{
  color: cornflowerblue;
}

.nav-link i:hover{
  color: cornflowerblue;
}

.nav-link i{
  color: black;
  transition: ease-in-out 0.5s;
  font-size: 14px;
}

.carticon{
  margin-top: 6px;
}

/*footer*/
footer {
  color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  z-index: -1;
  padding: 40px 80px;
}

footer .col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
  position: static;
}

footer .col a {
  font-size: 14px;
  color: wheat;
}

footer .col img {
  width: 170px;
  margin-left: -20px;
  margin-top: -10px;
}

footer .col .Follow h4 {
  margin-top: -20px;
}

footer .col h4 {
  font-size: 20px; /*change to 12 for mobile*/
  padding-bottom: 20px;
  font-weight: 700;
  padding-right: 20px;
}

footer p {
  font-size: 16px;
  margin: 0px 0 8px 0;
}

footer a {
  font-size: 13px;
  text-decoration: none;
  margin-bottom: 10px;
  color: #222;
}

.copyright {
  font-size: 20px;
  color: wheat;
  background: black;
}

footer .Follow {
  margin-top: 20px;
}

footer .Follow i {
  color: wheat;
  padding-right: 8px;
  cursor: pointer;
}

footer .Follow i:hover {
  color: red;
}

footer a:hover {
  color: red;
  text-decoration: none;
}

footer .install i {
  font-size: 30px;
}

footer .install i:hover {
  color: rgb(227, 230, 146);
}

footer .install button {
  margin-left: 10px;
  width: 90px;
  height: 40px;
  border: 1px solid #088178;
  border-radius: 6px;
}

footer .install .payment button {
  margin-left: -3px;
  width: 50px;
  height: 40px;
  border: 1px solid black;
  border-radius: 6px;
}

footer .install button:hover {
  background: rgb(104, 147, 184);
  transition: ease-out 0.5s;
}

/*not found page*/
.nf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 300px;
}

.heading,
.sub-heading {
  font-weight: normal;
}

.nf-btn {
  color: black;
  border: 1px solid black;
  border-radius: 4px;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.nf-btn:hover {
  background-color: rgb(155, 205, 228);
  color: white;
}

/*Contact*/
#contact-header {
  width: 100%;
  height: 40vh;
  padding: 14px;
  background-size: cover;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}

#contact-header h2 {
  color: black;
  font-weight: 700;
  font-size: 30px;
}

#contact-header p {
  color: black;
  font-size: 25px;
}

#contact-details {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#contact-details .details {
  width: 40%;
}

#contact-details .details span {
  font-size: 12px;
}

#contact-details .details h2,
#form-details form h2 {
  font-size: 26px;
  padding: 20px 0;
  line-height: 35px;
  font-weight: 800;
}

#contact-details .details h3 {
  font-size: 16px;
  padding-bottom: 15px;
  font-weight: 700;
}

#contact-details .details li {
  list-style: none;
  display: flex;
  padding: 10px 0;
}

#contact-details .details li i {
  font-size: 14px;
  padding-right: 22px;
}

#contact-details .details li p {
  font-size: 16px;
  margin: -6px;
}

#contact-details .map {
  width: 55%;
  height: 400px;
}

#contact-details .map iframe {
  width: 100%;
  height: 100%;
}

#form-details {
  padding: 80px;
  border: 1px solid #e1e1e1;
}

#form-details form {
  width: 65%;
  display: flex;
  flex-direction: column;
}

#form-details form h2 {
  margin-left: 40px;
}

#form-details form input,
#form-details form textarea {
  padding: 12px 15px;
  outline: none;
  width: 100%;
  margin-bottom: 20px;
  border: 1px solid #e1e1e1;
  margin-left: 40px;
}

#form-details form button {
  color: #fff;
  background-color: black;
  height: 50px;
  width: 100px;
  border-radius: 4px;
  margin-left: 40px;
}

#form-details form button:hover {
  background-color: #088178;
}

/*about*/
.aboutus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 200px;
}

/*policy*/
.policy {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/*register*/
.form-container {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  flex-direction: column;
}

.form-container form {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 20px;
  background-color: #fff;
}

.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  font-family: "Playfair Display", serif;
}

.form-container form input {
  border: 1px solid black;
  border-radius: 4px;
}

.form-container form button {
  border: 1px solid black;
  border-radius: 4px;
  color: white;
  width: 250px;
}

.form-container form button:hover {
  border: 1px solid black;
  transition: ease-in-out 0.5s;
}

/*admin products*/
.product-link{
  text-decoration: none !important;
  color: #000 !important;
}

.cart-page {
  margin-top: 64px;
}

.cart-page h1 {
  padding: 15px !important;
  font-family: "roboto", sans-serif;
  font-weight: normal;
  background-color: rgba(0, 0, 255, 0.072) !important;
}
.cart-page h1 > p {
  font-size: 20px;
  margin-top: 10px;
}

.cart-page .card {
  padding: 5px !important;
  height: 150px !important;
  margin-bottom: 5px !important;
}

.cards{
  margin-top: 40px;
}
.cart-page .cart-summary {
  text-align: center;
  margin-top: 40px;
}

.category {
  flex-wrap: wrap;
  margin-top: 80px !important;
}
.category .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
}
.category .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.category .card-price {
  color: green;
  font-weight: bold;
}
.category button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.category .card-text {
  color: rgb(90, 89, 89);
}

.category .card img:hover {
  transform: scale(0.9);
}

.category .card-img-top {
  height: 300px !important;
}

.product-details {
  margin-top: 100px !important;
}

.product-details-info {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
}

.product-details-info h6 {
  margin-bottom: 20px !important;
}

.similar-products .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
}

.similar-products .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.similar-products .card-price {
  color: green;
  font-weight: bold;
}
.similar-products button {
  border-radius: 4px;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.similar-products .card-text {
  color: rgb(90, 89, 89);
}

.similar-products .card img:hover {
  transform: scale(0.9);
}

.similar-products .card-img-top {
  height: 300px !important;
}
.similar-products h1{
  text-align: center;
  margin-top: -20px;
}
.banner-img {
  margin-top: 60px;
}

.home-page h1 {
  color: gray;
  font-family: "Playfair Display", serif;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
}
.home-page .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
}
.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price {
  color: green;
  font-weight: bold;
}
.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  height: 300px !important;
}
/* ==================
filters
============== */
.filters h4 {
  margin-top: 100px;
  color: gray !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
}

.ant-checkbox-wrapper {
  margin: 2px;
}
.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
}

.filters button {
  background-color: black;
  width: 100%;
  border: none;
  border-radius: 0;
  margin-top: 20px;
}

.search-icon {
  transform: translateY(-50%);
  color: #888;
  cursor: pointer;
  margin-top: 23px;
  margin-left: -36px;
  font-size: 14px;
}

.search-input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

body, h1, h2, h3, h4, h5, h6, .nav-link {
  font-family: 'Poppins', sans-serif;
}

.banner-img {
  max-height: 300px;
  object-fit: cover;
  width: 100%;
}

 .home-page {
  margin-top: 10px;
}

.filters h1, .home-page h1{
  color: #007BFF; /* Primary Color */
}

/* all products card */
.card {
  box-shadow: 0 4px 6px rgba(0,0,0,.1);
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0,0,0,.15);
}

.card-img-top {
  height: 200px;
  object-fit: cover;
}

.btn-info, .btn-secondary {
  margin: 5px;
}

.btn-danger {
  background-color: #dc3545; /* Bootstrap's default danger color for consistency */
}

/* Custom button reset filters styles */
.btn-danger {
  background-color: #dc3545; /* Bootstrap's default danger color */
  border: none;
  padding: 10px 20px;
  border-radius: 20px;
  transition: background-color 0.3s ease;
}

.btn-danger:hover {
  background-color: #c82333; /* A slightly darker shade for hover effect */
}

/* Custom styles for the 'Load More' button */
.loadmore {
  color: white;
  border: none;
  padding: 10px 20px;
  margin-left: -15px;
  border-radius: 20px;
  background-color: #0069d9;
}


/* Adjustments for mobile responsiveness */
@media (max-width: 768px) {
  .banner-img {
    max-height: 200px;
  }

  .card-img-top {
    height: 150px;
  }
}

@media(max-width:768px) {
  .search-form{
    margin-top: 8px;
  }
}

@media (max-width: 425px) {
  .similar-products .card {
    margin-left: 20px;
  }
  .filters{
    text-align: center;
  }
  .similar-products h1{
    margin-top: 10px;
  }
  .orderpage{
    overflow-x: auto;
  }
}
